import { useState } from 'react';

import './App.scss';
import { MoviesList } from './components/MoviesList';
import moviesFromServer from './api/movies.json';

export const App = () => {
  const [query, setQuery] = useState('');

  function filterFilmsByQuery(allMovies, filterQuery) {
    const movies = [...allMovies];

    const paramToFilter = (param) => {
      const preparedParam = param.toLowerCase();

      return (
        preparedParam.includes(filterQuery.toLowerCase())
      );
    };

    return movies.filter(movie => (
      paramToFilter(movie.title) || paramToFilter(movie.description)
    ));
  }

  const visibleMovies = filterFilmsByQuery(moviesFromServer, query);

  return (
    <div className="page">
      <div className="page-content">
        <div className="box">
          <div className="field">
            <label
              htmlFor="search-query"
              className="label"
            >
              Search movie
            </label>

            <div className="control">
              <input
                type="text"
                id="search-query"
                className="input"
                placeholder="Type search word"
                onChange={(event) => {
                  setQuery(event.currentTarget.value.trim());
                }}
              />
            </div>
          </div>
        </div>

        <MoviesList
          movies={visibleMovies}
        />
      </div>

      <div className="sidebar">
        Sidebar goes here
      </div>
    </div>
  );
};
